import React from 'react';
import PropTypes from 'prop-types';

import {
	Navigation, Row, Col, Button, Group,
	Form, Input, Radio, Checkbox, Menu,
	Modal, A, Icon,
} from '../../../components';

import { toString } from '../../utils/objectUtil';

class GroupPage extends React.Component {
	constructor() {
		super();
		this.state = {};
	}

	render() {
		return (
			<Row>
				<Col xs="1/3">
				</Col>

				<Col xs="2/3">
					<h1></h1>
					<p>
						
						<A href="http://fontawesome.io" target="_blank">Font Awesome</A>
						
							<A href="http://iconfont.cn/" target="_blank">iconfont</A>
						
					</p>

					<h2></h2>
					<p>
						Bamboo UIFont Awesome css
					</p>
					<pre className="code">
						{'<link href="[]/font-awesome.min.css" rel="stylesheet">'}
					</pre>

					<h2></h2>
					<p>
						<code>Icon</code><code>name</code>
						<code>spin</code>
					</p>
					<div className="measurement">
						<div className="preview">
							<Icon name="smile-o" />
							{' '}
							<Icon name="refresh" spin />
						</div>
						<pre className="code">
							{`
<Icon name="smile-o" />
<Icon name="refresh" spin />
`.trim()}
						</pre>
					</div>

					<h2></h2>
					<p>
						
						<A href="http://fontawesome.io" target="_blank">Font Awesome</A>
						
						
						<code>Icon.Loading</code>
					</p>
					<div className="measurement">
						<div className="preview">
							<Icon.Loading />
							{' '}
							<Icon.Loading id={1} />
							{' '}
							<Icon.Loading id={2} />
							{' '}
							<Icon.Loading id={3} />
							{' '}
							<Icon.Loading id={4} />
						</div>

						<pre className="code">
							{`
<Icon.Loading />
<Icon.Loading id={1} />
<Icon.Loading id={2} />
<Icon.Loading id={3} />
<Icon.Loading id={4} />
`.trim()}
						</pre>
					</div>
				</Col>
			</Row>
		);
	}
}

GroupPage.propTypes = {
	history: PropTypes.object,
};

export default GroupPage;



