import React from 'react';
import PropTypes from 'prop-types';

import {
	Navigation, Row, Col, Button, DynamicNumber,
	Form, Input, Radio, Checkbox, Tooltip, AutoComplete,
} from '../../../components';

import { TYPE_LIST, SIZE_LIST } from '../../utils/enum';
import { toString } from '../../utils/propsUtil';

class AutoCompletePage extends React.Component {
	constructor() {
		super();
		this.state = {
			form: {},
			value: '',
		};
	}

	getSampleCode = () => {
		return `
<AutoComplete matcher={matcherList} value={...} onChange={...} />
`.replace(/\t/g, '   ').trim();
	};

	render() {
		const { form } = this.state;

		const matcherList = [
			{
				value: 'ABCDE',
			}, {
				value: 'FGHIJ',
			}, {
				value: 'KLMNO',
			}, {
				value: 'PQRST',
			}, {
				value: 'UVWXYZ',
			},
			'ASD',
			'BCD',
			'EGG',
			'GOOD',
			'BAD',
		];

		return (
			<Row>
				<Col xs="1/3">
				</Col>

				<Col xs="2/3">
					<h1></h1>
					<p>
						<code>onKeyDown</code><code>onKeyUp</code>
					</p>

					<h2></h2>
					<div className="measurement">
						<div className="preview">
							<Form instance={this}>
								<Form.Field name="value">
									<AutoComplete matcher={matcherList} />
								</Form.Field>
							</Form>
						</div>
						<div className="form">
							<pre>
								{'const matcherList = '}
								{JSON.stringify(matcherList, null, 3)}
							</pre>
						</div>

						<pre className="code">
							{this.getSampleCode()}
						</pre>
					</div>
				</Col>
			</Row>
		);
	}
}

AutoCompletePage.propTypes = {
	history: PropTypes.object,
};

export default AutoCompletePage;



